import React from 'react'
import '../css/errorpage.css'
import lost from '../images/lost.svg'
import {Helmet} from 'react-helmet'

export default function ErrorPage({lang}) {
    const text = {en:['Oops! Not the place!',
                    `How did you get here? Anyways, let's get you back where you should. Click one of those pretty links up top!`,
                    '404 Error: Not Found'],
                es:['¡Oops! ¡Aquí no es!',
                    '¿Cómo llegaste aqui? De todas maneras, vamos a donde deberías. ¡Da clic en uno de esos lindos enlaces allá arriba!',
                    'Error 404: No Encontrado']}
    return (
        <>
            <Helmet>
                <title>Error 404: Not Found</title>
            </Helmet>
            <div className='error-page'>
                <img src={lost} alt='looking for happiness' className="lost"/>
                <span className="bigtext">
                    {text[lang][0]}
                </span>
                <p className="smol-text">{text[lang][1]}</p>
                <div className="header error">{text[lang][2]}</div>
            </div>
        </>
    )
}
